@import url(https://fonts.googleapis.com/css2?family=Cutive+Mono&display=swap);
body {
    font-size: 1.1rem;
    font-weight: 500;
    font-family: 'Cutive Mono', monospace;
    margin: 0;
    padding: 0;
}

html {
    margin: 0;
    padding: 0;
}

* {
    box-sizing: border-box;
}

a {
    color: black;
    text-decoration: none;
}

a:hover {
    background-image: linear-gradient(90deg, #c793cb, #ec8c9c);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
}

a:visited {
    color: black;
}
.app{height:100vh;width:100vw;display:flex;flex-direction:column}
.header{margin:1rem 1rem;box-sizing:border-box}
.home{flex:0 1 100%;width:100%}.home-menu{height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.home-menu a{padding:.5rem 0 .5rem}
.resume{height:100%;display:flex;flex-direction:column;align-items:center;overflow-y:scroll}.resume-document{height:auto;width:100%}.resume-loading{height:100%;display:flex;align-items:center}
